.home-content {
  margin: 134px auto 0 auto;
  max-width: 768px;
}
@-webkit-keyframes rotated {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotated {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.list-loader {
  width: 100px;
  margin: auto;
}
.list-loading {
  -webkit-animation: 800ms linear 0s infinite rotated;
          animation: 800ms linear 0s infinite rotated;
}



